.formStyles  {
    width: 100%;
    height: 45px;
    display: flex; 
    justify-content: center; 
    margin-top: 30px;

}

.inputStyles {
    width: 70%;
    border-style: solid;
    border-width: thin;
    border-color: lightgrey;
    border-radius: 3px;
    font-size: 12px;
    font-weight: 200;
    font-style: italic;
    letter-spacing: 2.5px;
    padding-left: 10px;
    opacity: 0.7;
    

}

.btnStyles {
    width: 14%;
    margin-left: 5px;
    border-style: solid;
    border-width: thin;
    border-color: #6EE7B7;
    background-color: #6EE7B7;
    color: white;
    letter-spacing: 2px;
    font-size: 15px;
    font-weight: 900;
    border-radius: 3px;
}

@media only screen and (max-width: 748px) {

    .btnStyles {
        width: 14%;
        margin-left: 5px;
        border-style: solid;
        border-width: thin;
        border-color: #6EE7B7;
        background-color: #6EE7B7;
        color: white;
        letter-spacing: 2px;
        font-size: 15px;
        font-weight: 900;
        border-radius: 3px;
        padding: 0;
        background-clip: padding-box;
    }
}
